import React from 'react'
import { UilSetting } from '@iconscout/react-unicons'
import home from '../../img/home.png'
import noti from '../../img/noti.png'
import cmt from '../../img/comment.png'
import back from '../../img/return.png'
import './RightSide.css'
import { TrendCard } from './../TrendCard/TrendCard';
import { useDispatch } from 'react-redux';
import { logout } from '../../actions/AuthAction'
import { HomeMessage } from './../HomeMessage/HomeMessage';
import { Link } from 'react-router-dom';
function RightSide() {
    const dispatch = useDispatch()
    const handleLogOut = () => {
        dispatch(logout())
        // truyen1 function()
    }
    return (
        <div className="RightSide">
            {/* react-unicons */}
            <div className="navIcons">
                <img onClick={handleLogOut} src={back} alt="" />
                <UilSetting ></UilSetting>
                <img src={noti} alt="" />
                <Link to='/SocialMedia_N_APP/chat'> <img src={cmt} alt="" /></Link>
            </div>
            <HomeMessage></HomeMessage>
            <TrendCard></TrendCard>
            <button className='button'>Share</button>

        </div>
    )
}

export default RightSide