import React from 'react'
import "./Message.css"
export const Message = ({ data, auth }) => {

    return (
        <div className="Message">
            {data.senderUserId === auth ? <>
                <h1 className='yourMessage'>{data.content}</h1>
                {/* <p>{data.createdAt}</p> */}



            </> : <>
                <h1 className='otherMessage'>{data.content}</h1>
                {/* <p>{data.createdAt}</p> */}



            </>}
        </div>
    )
}
