import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { getUser } from '../../api/AuthAPi';
import defaultAvatart from '../../img/defaultAvatar.jpg'
import './ChatRoom.css'
export const ChatRoom = ({ data, setRoomId }) => {
    const auth = useSelector(state => state.AuthReducer.user)
    const [user, setUser] = useState(null)

    useEffect(() => {
        const userId = data.members.find((id) => id !== auth._id)
        let secondUser = async () => {
            try {
                console.log(userId);
                let userInfo = await getUser(userId)
                console.log(userInfo, "id");
                setUser(userInfo.data)
            } catch (error) {
                console.log(error);
            }
        }
        secondUser()
    }, [])
    const handlesetRoomId = (roomId) => {
        setRoomId(roomId)
    }
    return (
        <div className="ChatRoom">
            {

                user && <div className='profile'>
                    <img src={user.profilePicture ? `http://localhost:6060/${user.profilePicture}.png` : defaultAvatart} alt="" />
                    <div className="userInfoChat" >
                        <h1 onClick={() => { handlesetRoomId(data._id) }}>{user.firstname} {user.lastname}</h1>
                        <p>@{user.username}</p>
                    </div>
                </div>
            }
        </div>
    )
}
