import "./App.css"
import Home from './pages/Home';
import { Profile } from './pages/ProfilePage/Profile';
import { Auth } from './pages/Auth/Auth';
import { Route, Routes, Navigate } from "react-router-dom";
import { useSelector } from 'react-redux';
import { Chat } from './pages/Chat/Chat';
function App() {
  let user = useSelector((state) => state.AuthReducer.user)
  return (
    <div className="App">
      {/* <div className="blur" style={{
        position: 'absolute',
        top: '30%',
        left: '-110px'

      }}></div>
      <div className="blur" style={{
        position: 'absolute',
        top: '-120px',
        right: '10%',

      }}></div> */}
      <Routes>
        {/*domain.com/ di tu route goc là /
        vd /SocialMedia_N_APP/
        to "abc" la ghi đè tình từ gốc là                  /abc
        to "/abc" dấu / là copy url đằng trước gán vào gốc /.../abc
        to "../abc" là về gốc /abc
      */}

        <Route path="/SocialMedia_N_APP/" element={user ? <Navigate to="../SocialMedia_N_APP/home" /> : <Navigate to="../SocialMedia_N_APP/auth" />} />

        {/*di tu route goc là domain.com/   */}
        <Route path="/SocialMedia_N_APP/home" element={user ? <Home /> : <Navigate to="../SocialMedia_N_APP/auth" />} />
        <Route path="/SocialMedia_N_APP/auth" element={!user ? <Auth /> : <Navigate to="../SocialMedia_N_APP/home" />} />
        <Route path="/SocialMedia_N_APP/profile/:userID" element={user ? <Profile /> : <Navigate to="../SocialMedia_N_APP/auth" />} />
        <Route path="/SocialMedia_N_APP/chat/" element={user ? <Chat /> : <Navigate to="../SocialMedia_N_APP/auth" />} />

      </Routes>
    </div>
  );
}

export default App;
