import axios from 'axios'
export const signUp = async (data) => {
    return await axios.post('http://localhost:6060/auth/register', data)
}
export const login = async (payload) => {
    return await axios.post('http://localhost:6060/auth/login', payload)
}
export const allUser = async (id) => {
    return await axios.get(`http://localhost:6060/auth/allUser/${id}`)
}
export const getUser = async (id) => {
    return await axios.get(`http://localhost:6060/users/${id}`)
}
export const updateUser = async (id, payload) => {
    return await axios.post(`http://localhost:6060/users/${id}`, payload)
}