import React, { useEffect, useRef, useState } from 'react'
import { createChat, GetAllPrivateChat, GetRoom } from './../../api/ChatAPI';
import { useSelector } from 'react-redux';
import * as AuthAPI from '../../api/AuthAPi'
import './Chat.css'
import { ChatRoom } from './../../components/Room/ChatRoom';
import { Message } from '../../components/Messages/Message';
export const Chat = () => {
    const [allRoom, setAllRoom] = useState()
    const [currentRoomId, setCurrentRoomId] = useState()
    const [allMessages, setAllMessages] = useState()
    const auth = useSelector(state => state.AuthReducer.user._id)
    const chatRef = useRef()
    const listRef = useRef()


    //get chatroom
    useEffect(() => {
        const getRooms = async () => {
            try {
                let { data } = await GetRoom(auth)

                setAllRoom(data)

            } catch (error) {

            }
        }
        listRef.current?.lastElementChild?.scrollIntoView({ block: 'end', behavior: 'smooth' });

        getRooms()

    }, [auth])
    useEffect(() => {
        const getPrivateMessage = async () => {
            try {
                let { data } = await GetAllPrivateChat(currentRoomId)
                console.log(data);
                setAllMessages(data)
            } catch (error) {
            }
        }
        getPrivateMessage()
        listRef.current?.lastElementChild?.scrollIntoView({ block: 'end', behavior: 'smooth' });


    }, [currentRoomId])

    // create message 
    const handleCreateMessage = async (e) => {
        e.preventDefault()

        let chatData = {
            roomId: currentRoomId,
            senderUserId: auth,
            content: chatRef.current.value,
        }
        chatRef.current.value = ''
        setAllMessages([...allMessages, chatData])
        console.log(chatData);
        let result = await createChat(chatData)
        listRef.current?.lastElementChild?.scrollIntoView({ block: 'end', behavior: 'smooth' });
    }
    return (

        <div className="Chat">
            {console.log(currentRoomId)}
            <div className="roomSection">
                <h1>Chat Room</h1>
                {allRoom && allRoom.map(element => {
                    return <>
                        <ChatRoom setRoomId={setCurrentRoomId} data={element}></ChatRoom>
                    </>
                })}
            </div>
            {console.log("checkMess", allMessages)}
            <div className="messageSection">

                {currentRoomId ?
                    <>
                        <div className="messageShow" ref={listRef}>
                            {allMessages && allMessages?.map(mess => {
                                return <Message data={mess} auth={auth}></Message>
                                // map phai co return
                            })}
                        </div>
                        <form >
                            <input ref={chatRef} type="text" placeholder='Enter Your message...' />
                            <button onClick={(e) => { handleCreateMessage(e) }}>Send</button>
                        </form></>

                    :
                    <>Click to another people to chat...</>}

            </div>
        </div>
    )
}
