import React from 'react'
import './HomeMessage.css'
import homeMessage from '../../img/homeMessage.png'
import cover from '../../img/cover.jpg'
import search from '../../img/search.png'
export const HomeMessage = () => {
    return (
        <div className="HomeMessage">
            <div className="title">
                <h1>Message</h1>
                <img src={homeMessage} alt="" />
            </div>
            <div className="searchMessage">
                <img src={search} alt="" />
                <input type="text" placeholder='Search Messages...' />
            </div>
            <div className="messageTabs">
                <div className="tab_" > <p>Primary</p> </div>
                <div className="tab_" > <p>General</p> </div>
                <div className="tab_" > <p>Request (7)</p> </div>
            </div>
            <div className="allUsersInfo">
                <div className="userInfo">
                    <img src={cover} alt="" />
                    <div className="userName">
                        <h1>GiangTran</h1>
                        <p>Hello, What happening</p>
                    </div>
                </div>
                <div className="userInfo">
                    <img src={cover} alt="" />
                    <div className="userName">
                        <h1>GiangTran</h1>
                        <p>Hello, What happening</p>
                    </div>
                </div>

                <div className="userInfo">
                    <img src={cover} alt="" />
                    <div className="userName">
                        <h1>GiangTran</h1>
                        <p>Hello, What happening</p>
                    </div>
                </div>
            </div>
        </div>
    )
}
