import axios from 'axios'

export const UploadPost = async (id) => {
    // return response de gan cho res
    return await axios.get(`http://localhost:6060/users/${id}`)

}
export const FollowUser = async (id, userId) => {
    // return response de gan cho res
    await axios.put(`http://localhost:6060/users/${id}/follow`, { currentUserId: userId })

}
export const UnFollowUser = async (id, userId) => {
    // return response de gan cho res
    await axios.put(`http://localhost:6060/users/${id}/unfollow`, { currentUserId: userId })

}