import axios from 'axios'

export const UploadPost = async (data) => {
    // return response de gan cho res
    return await axios.post('http://localhost:6060/post/create', data)

}
export const UploadFile = async (data) => {
    // return response de gan cho res
    return await axios.post('http://localhost:6060/file/upload', data)

}
export const TimeLine = async (id) => {
    // return response de gan cho res
    return await axios.get(`http://localhost:6060/post/${id}/timeline`)

}
export const likePost = async (id, body) => {
    // return response de gan cho res
    await axios.put(`http://localhost:6060/post/${id}/like`, body)

}
export const unlikePost = async (id, body) => {
    // return response de gan cho res
    await axios.put(`http://localhost:6060/post/${id}/unlike`, body)

}
export const allUserPosts = async (id) => {
    // return response de gan cho res
    return await axios.get(`http://localhost:6060/post/${id}/userPosts`)

}