import React from 'react'
import './PostSide.css'
import PostShare from './../PostShare/PostShare';
import Posts from './../Posts/Posts';
import { Story } from './../Story/Story';

const PostSide = () => {
    return (
        <div className="PostSide">
            <Story></Story>

            <PostShare></PostShare>
            <Posts></Posts>

        </div>
    )
}

export default PostSide
