import axios from 'axios'
export const GetRoom = async (userID) => {

    return await axios.get(`http://localhost:6060/chat/${userID}`)

}
export const CreateRoom = async (data) => {

    return await axios.post(`http://localhost:6060/chat/`, data)

}
export const CheckExists = async (data) => {

    return await axios.post(`http://localhost:6060/chat/checkExistsRoom`, data)

}
export const GetAllPrivateChat = async (roomId) => {

    return await axios.get(`http://localhost:6060/message/${roomId}`)

}
export const createChat = async (data) => {
  
    return await axios.post(`http://localhost:6060/message/`, data)


}
