import React, { useState } from 'react'
import './Post.css'
import { BiHeart } from "react-icons/bi";
import { BiCommentDetail } from "react-icons/bi";
import Liked from '../../img/like.png'
import { useSelector, useDispatch } from 'react-redux';
import { getTimeLine, handleLikePost, handleUnLikePost } from './../../actions/PostAction';
import defaultAvatar from '../../img/defaultAvatar.jpg'
import like from '../../img/like.png'
import angry from '../../img/angry.png'
import care from '../../img/care.png'
import wow from '../../img/wow.png'
import sad from '../../img/like.png'
import haha from '../../img/haha.png'
import heartNoneButton from '../../img/heart-post.png'
import heartButton from '../../img/heart.png'
import commentButton from '../../img/comments.png'
import sendButton from '../../img/send.png'

function Post({ data }) {
    let dispatch = useDispatch()

    let currentUserID = useSelector(state => state.AuthReducer.user._id)
    const [isLiked, setIsLiked] = useState(data.likes.includes(currentUserID))
    const [likes, setLikes] = useState(data.likes.length)

    const handleLike = () => {
        setLikes(prev => prev + 1)
        console.log('like');
        setIsLiked(true) // set view
        dispatch(handleLikePost(data._id, { currentUserId: currentUserID })) // set server side and update to redux
        // khi nao refesh lai thi se lay timeline
        // nen khong can get Timeline khi like, unlike
        //chay theo tuan tu await
    }
    const handleUnLike = () => {
        setLikes(prev => prev - 1)


        setIsLiked(false)

        console.log("unlike");
        dispatch(handleUnLikePost(data._id, { currentUserId: currentUserID }))


    }
    return (
        <div className="Post">
            <div className="user-posting">
                <img src={defaultAvatar} alt="" />
                <div className="user_info">
                    <h1>GiangVn</h1>
                    <p>@username</p>
                </div>

            </div>
            <p> {data.desc}</p>
            <div className="posting-img">
                <img width='100%' src={`http://localhost:6060/${data.image}`} alt="" />
                <div className="more-img">
                    <img width='100%' src={`http://localhost:6060/${data.image}`} alt="" />
                    <img width='100%' src={`http://localhost:6060/${data.image}`} alt="" />
                    <div className="more-img-count">+3</div>
                </div>
            </div>
            <div className="iconReact">
                {/* {!isLiked ? <BiHeart onClick={handleLike} ></BiHeart> : <img onClick={handleUnLike} src={Liked} alt="" />

                } */}

                <h1>
                    <p>{likes}</p>
                    {!isLiked ? <img onClick={handleLike} className='heart-btn' src={heartNoneButton} alt="" /> : <img onClick={handleUnLike} className='heart-btn' src={heartButton} alt="" />}
                    <div className="emotion">
                        <img src={like} alt="" />
                        <img src={angry} alt="" />
                        <img src={care} alt="" />
                        <img src={wow} alt="" />
                        <img src={sad} alt="" />
                        <img src={haha} alt="" />
                    </div>
                    <p>{likes}</p>

                    <img src={commentButton} alt="" />
                    <p>{likes}</p>

                    <img src={sendButton} alt="" />

                </h1>

            </div>
            <div className="commentInput">
                <img src={defaultAvatar} alt="" />
                <input type="text" placeholder='write your react...' />
                <img src={sendButton} alt="" />
            </div>
            {/* <div className="likes">
                <p> Likes </p>
            </div> */}
            {/* <div className="desc">
                <p> {data.desc}</p>
            </div> */}
        </div>

    )
}

export default Post
